import http from '../../../utils/http.js'
import MyHeader from '../../../components/header/header.vue';
import uniLoadMore from '../../../components/uni-load-more.vue'
import {
	allItems,
	daifukuan,
	daifahuo,
	daishouhuo,
	daipingjia
} from './config.js';
export default {
	data() {
		return {
			// 订单列表
			items: [],
			product_list: [],
			// 用于判断订单操作按钮显示或隐藏的数组,当点击第几个订单导航时,这个数组第几个选项的值设为true
			arr: [1, '', '', '', ''],
			// 搜索图标旁边的数字红标数据
			newsNumber: 66,
			//保存当前导航激活项的索引值
			navItemID: '0',
			status: '',
			statu: '',
			page: 1,
			size: 4,
			hasMoreData: true//上拉时是否继续请求数据，即是否还有更多数据
		}
	},
	components: {
		MyHeader,
		uniLoadMore
	},
	updated() {},
	methods: {
		// 跳转到订单详情页
		toDeatil(order_id,st) {
			console.log(order_id)
			uni.navigateTo({
				url: './dingdanXiangqing?order_id=' + order_id+'&statu='+st,
				success: res => {},
				fail: () => {},
				complete: () => {}
			})
		},
		// 删除订单
		delOrder(order_id) {
			console.log(order_id)
			var orderId = order_id
			uni.showModal({
				title: '提示',
				content: '是否确认删除',
				success: function(res) {
					if (res.confirm) {
						http.httpTokenRequest({
							// url:'getMyOrder?login_id='+this.$store.state.login_id+'&status='+this.status,
							url: 'delOrder',
							method: 'post'
						}, {
							"order_id": orderId
						}).then(res => {
							if (res.data.code == 200) {
								uni.showToast({
									title: '删除成功！',
									icon: 'none',
									duration: 2000
								})
							}
						}, error => {
							console.log(error);
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
		},
		//清除所有nav元素激活样式
		clean(navItem) {
			for (let i = 0; i < navItem.length; i++) {
				navItem[i].className = 'navItem'
			}
		},
		// 点击导航改变订单展示区
		changeDingdan(e) {
			this.page = 1
			// 判断如果点击当前选中选项,什么也不做
			if (this.navItemID === e.dataset.id) {
				return;
			}
			// 获取nav导航元素组
			var navItem = this.$el.querySelector('#nav').querySelectorAll('.navItem');
			this.status = e.dataset.id
			this.getOrder()
			console.log(e.dataset.id);
			// 修改订单显示区数据源对应当前点击选项
			this.navItemID = e.dataset.id;
			// this.items = this.arr[this.navItemID];
			// 订单操作图标开关
			this.arr = ['', '', '', '', ''];
			this.arr[this.navItemID] = true;
			// 切换选项卡样式
			this.clean(navItem);
			navItem[this.navItemID].className = 'navItem active';
		},
		// 获取我的订单
		getOrder: function(message) {
			uni.showNavigationBarLoading() //在当前页面显示导航条加载动画
			uni.showLoading({ //显示 loading 提示框
				title: message,
			})
			http.httpTokenRequest({
				// url:'getMyOrder?login_id='+this.$store.state.login_id+'&status='+this.status + '&page=' + this.page + '&size=' + this.size,
				url: 'getMyOrder?login_id=1027&status=' + this.status + '&page=' + this.page + '&size=' + this.size,
				method: 'get'
			}, {}).then(res => {
				console.log(res)
				var allOrder = this.items
				if (res.data.code == 200) {
					if (res.data.data.length >= 0) {
						uni.hideNavigationBarLoading()
						uni.hideLoading()
						if (this.page == 1) {
							allOrder = []
						}
						var allorder = res.data.data
						console.log("下一頁加載出來的數據",allorder)
						if (allorder.length < this.size) {
							this.items = allOrder.concat(allorder)
							this.hasMoreData = false
						} else {
							this.items = allOrder.concat(allorder)
							this.hasMoreData = true
							this.page++
							console.log(this.items)
						}
					}
					// this.items = res.data.data
					for (var i = 0; i < res.data.data.length; i++) {
						this.product_list.push(res.data.data[i].product_list[0])
					}
					if (this.status == 0) {
						this.statu = '全部'
					} else if (this.status == 1) {
						this.statu = '待付款'
					} else if (this.status == 2) {
						this.statu = '待发货'
					} else if (this.status == 3) {
						this.statu = '待收货'
					} else if (this.status == 4) {
						this.statu = '待评价'
					}
					console.log("获取到的订单", res.data.data);
					console.log("获取到的订单列表", this.product_list);
				}
			}, error => {
				console.log(error);
			})
		}
	},
	created() {
		
		// 定义一个数组保存各类数据的索引
		this.arr2 = [allItems, daifukuan, daifahuo, daishouhuo, daipingjia];
		//初始化订单数据
		this.items = this.arr2[this.navItemID]
	},
	mounted() {},
	/**
	 * 页面相关事件处理函数--监听用户下拉动作
	 */
	onLoad: function() {
		// 获取订单列表
		this.getOrder('正在加载数据...')
	},
	onPullDownRefresh: function() {
		console.log("下拉")
		this.page = 1
		this.getOrder('正在刷新数据')
	},

	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: function() {
		console.log("上拉" + this.hasMoreData)
		console.log("當前頁面",this.page)
		if (this.hasMoreData) {
			this.getOrder('加载更多数据')
		} else {
			uni.showToast({
				title: '没有更多数据',
				icon:"none"
			})
		}
	},
}
